video {
  object-fit: cover;
  width: 100%;
  height: 110%;
  position: fixed;
  z-index: -1;
  margin-top: -160px;
}

.hero-container {
  /* background: url('/images/img-home.jpg') center center/cover no-repeat; */
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
  object-fit: contain;
}

.hero-container > h1 {
  color: #fff;
  font-size: 35px;
 margin-left : 120px;
 margin-right: 120px;
  text-shadow: 3px 2px 1px  rgba(2, 183, 183, 0.699),-3px -2px 1px  rgba(183, 2, 2, 0.616);
  
}

.hero-container > p {
  margin-top: 8px;
  color: #fff;
  font-size: 32px;

}

.hero-btns {
  margin-top: 32px;
}

.hero-btns .btn {
  margin: 6px;
}

.fa-play-circle {
  margin-left: 4px;
}

@media screen and (max-width: 960px) {
  .hero-container > h1 {
    font-size: 27x;
    margin-left : 80px;
 margin-right: 80px;
  }
}

@media screen and (max-width: 768px) {
  .hero-container > h1 {
    font-size:23px;
    margin-left : 50px;
 margin-right: 50px;
  }

  .hero-container > p {
    font-size: 30px;
  }

  .btn-mobile {
    display: block;
    text-decoration: none;
  }

  .btn {
    width: 100%;
  }
}
